.main {
  background-color: black;
  height: 100vh;
  padding: 50px;
}

.name_header {
  text-align: right;
  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 20px;
  margin-top: 20px; /* Adjust as needed */
  margin-right: 100px;
}

.header {
  text-align: center;
  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 20px;
  top: 0;
  position: top;

}


.menu-list {
  list-style-type: none;
  font-family: 'Times New Roman', Times, serif;
  font-size: 18px;
  color: white;
  padding: 0;
}

.menu-list li a {
  color: inherit;
  text-decoration: none;
}

.menu-list li a:hover {
  text-decoration: underline;
}

.menu-list li {
  margin-bottom: 10px;
  cursor: pointer;
}

.menu-list li:hover {
  text-decoration: underline;
}


.logo-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  max-width: 30%;
  width: auto;
  height: auto;
}

.home-logo {
  max-width: 5%;
  width: auto;
  height: auto;
}

.home-logo-container {
  flex: 1;
  display: flex;
  justify-content: left;
  align-items: left;
  padding-bottom: 30px;
}

.about-container {
  font-family: 'Times New Roman', Times, serif;
  font-size: 18px;
  color: white;
  margin: 5px 0;
  margin-left: 200px;
  margin-right: 200px;
  margin-top: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.pfp {
  max-width: 20%; /* Change this to 100% to make sure the image doesn't exceed its container */
  height: auto;
  display: block; /* This ensures that the image is a block element */
  margin: 0 auto; /* Center the image horizontally */
}

.pfp_container {
  position: top;
  display: flex;
  justify-content: center;
  align-items: center;
}
